/* font definition: latin characters */
@font-face {
  font-family: "Josefin Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Josefin Sans Regular"), local("JosefinSans-Regular"),
    url(https://fonts.gstatic.com/s/josefinsans/v12/Qw3aZQNVED7rKGKxtqIqX5EUDXx4.woff2)
      format("woff2");
  unicode-range: U + 0000-00ff, U + 0131, U + 0152-0153, U + 02bb-02bc, U + 02c6,
    U + 02da, U + 02dc, U + 2000-206f, U + 2074, U + 20ac, U + 2122, U + 2191,
    U + 2193, U + 2212, U + 2215, U + FEFF, U + FFFD;
}

/* general styling */
body {
  margin: 0;
  padding: 0;

  font-family: "Josefin Sans", "Verdana";
  color: #1c2f2f;

  background-color: #d4e157;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

/* set the color of the links, aka FontAwesome icons */
a:link,
a:visited,
a:hover {
  color: black;
}

/* page load animations */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.Introduction,
.Tag,
.SocialButtons {
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}



